body {
  font-family: "Open Sans", sans-serif;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between; }

.page-title {
  height: 350px; }

.fogging {
  display: none; }

.fogging-active {
  display: block;
  position: fixed;
  width: 100vw;
  height: 100vh;
  background: #c5c5c5a1;
  transition: .1s linear;
  z-index: 5; }

header {
  background: url(../img/bg-top.jpg); }
  header .main-info {
    width: 100%;
    position: absolute;
    top: 33%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #ffffff;
    text-align: center;
    text-transform: uppercase; }
    header .main-info h1 {
      font-size: 25px; }
  header .center-block nav {
    position: relative;
    border-bottom: 1px solid #ffffff5e;
    display: flex;
    justify-content: space-between;
    height: 100px;
    padding: 0 10px;
    box-sizing: border-box; }
    header .center-block nav .header-list:nth-child(1) {
      list-style: none;
      padding: 0; }
      header .center-block nav .header-list:nth-child(1) .logo img {
        width: 75px; }
    header .center-block nav .header-list:nth-child(2) {
      list-style: none;
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      align-items: center;
      padding: 15px 0;
      margin: 0;
      position: absolute;
      left: 50%;
      transform: translate(-50%, -100%);
      width: 250px;
      height: 300px;
      transition: .1s linear; }
      header .center-block nav .header-list:nth-child(2) .header-list-item {
        margin-bottom: 2px; }
      header .center-block nav .header-list:nth-child(2) .header-list-link {
        text-decoration: none;
        color: #ffffff;
        text-transform: uppercase;
        font-size: 14px;
        padding: 4px 6px;
        margin: 0 4px 0;
        border-bottom: 1px solid #ffffff5e;
        box-sizing: border-box;
        transition: .2s linear; }
    header .center-block nav .active {
      transform: translate(-50%, 70px) !important;
      background: #f9f9f9eb;
      border-radius: 2px;
      z-index: 10; }
      header .center-block nav .active .close-button {
        padding: 0;
        margin: 0;
        width: 25px;
        height: 25px;
        position: absolute;
        border: none;
        background: transparent;
        right: -25px;
        top: -25px;
        cursor: pointer; }
        header .center-block nav .active .close-button:before {
          content: '';
          position: absolute;
          width: 100%;
          height: 1px;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%) rotate(45deg);
          background: #fff; }
        header .center-block nav .active .close-button:after {
          content: '';
          position: absolute;
          width: 100%;
          height: 1px;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%) rotate(-45deg);
          background: #fff; }
      header .center-block nav .active .header-list-item {
        padding: 15px 0; }
        header .center-block nav .active .header-list-item .header-list-link {
          color: #4c4c4c !important; }
    header .center-block nav .hamburger {
      position: absolute;
      padding: 0;
      margin: 0;
      border: none;
      background: transparent;
      right: 10px;
      bottom: 30px;
      width: 25px;
      height: 21px;
      cursor: pointer;
      transition: .2s linear; }
      header .center-block nav .hamburger span {
        position: absolute;
        width: 100%;
        left: 0;
        height: 1px;
        background: #fff; }
        header .center-block nav .hamburger span:nth-child(1) {
          top: 0; }
        header .center-block nav .hamburger span:nth-child(2) {
          top: 10px; }
        header .center-block nav .hamburger span:nth-child(3) {
          bottom: 0; }
    header .center-block nav .hamburger-active {
      transform: translateX(50px); }

.animated-bg {
  -webkit-animation: animate-bg 800s linear infinite;
  -o-animation: animate-bg 800s linear infinite;
  animation: animate-bg 800s linear infinite;
  background-repeat: repeat-x; }

@-webkit-keyframes animate-bg {
  from {
    background-position: 0 0; }
  to {
    background-position: -50000px 0; } }

@-moz-keyframes animate-bg {
  from {
    background-position: 0 0; }
  to {
    background-position: -50000px 0; } }

@-o-keyframes animate-bg {
  from {
    background-position: 0 0; }
  to {
    background-position: -50000px 0; } }

@keyframes animate-bg {
  from {
    background-position: 0 0; }
  to {
    background-position: -50000px 0; } }

footer {
  background: #4c4c4c;
  height: 200px; }

@media (min-width: 768px) {
  .center-block {
    width: 768px;
    margin: 0 auto; }
  .page-title {
    height: 500px; }
  .animated-bg {
    -webkit-animation: animate-bg 900s linear infinite;
    -o-animation: animate-bg 900s linear infinite;
    animation: animate-bg 900s linear infinite;
    background-repeat: repeat-x; }
  .hamburger {
    display: none; }
  header .center-block nav .header-list:nth-child(2) {
    width: auto;
    height: auto;
    background: transparent;
    list-style: none;
    flex-direction: row;
    align-items: flex-end;
    padding: 15px 0;
    margin: 0;
    position: relative;
    transform: translate(0, 0);
    left: 0; }
    header .center-block nav .header-list:nth-child(2) button {
      display: none; }
    header .center-block nav .header-list:nth-child(2) .header-list-item {
      margin-bottom: 2px; }
    header .center-block nav .header-list:nth-child(2) .header-list-link {
      text-decoration: none;
      color: #ffffff;
      text-transform: uppercase;
      font-size: 14px;
      padding: 4px 10px;
      margin: 0 4px 0;
      border-bottom: 1px solid #ffffff5e;
      box-sizing: border-box;
      transition: .1s linear; }
      header .center-block nav .header-list:nth-child(2) .header-list-link:hover {
        background: #ffffff;
        color: #4c4c4c;
        border-bottom: 1px solid #ffffff;
        border-radius: 2px; } }

@media (min-width: 1024px) {
  .center-block {
    width: 1024px; } }

@media (min-width: 1200px) {
  .center-block {
    width: 1200px; } }
